@import "../../global.scss";

.projects{
    display: flex;
    flex-direction: column;
    background-color: whitesmoke;
    .top{
        flex: 0.1;
        text-align: center;
        
        background-color: whitesmoke;
        display: flex;
        .left{
            margin-top: 15px;
            flex: 0.5;
            background-color: whitesmoke;
            
        }
        .right{
            flex: 0.5;
            background-color: $mainColor;
        }
    }

    .bottom{
        flex: 0.9;
        display: flex;
        .left{
            flex: 0.5;
            background-color: whitesmoke;
            .content{
                margin: 20px 50px;
                h3{
                    margin-top: 20px;
                    font-weight: 500;
                }
                .nfont{
                    font-weight: 400;
                }
                .link{
                    font-weight: 500;
                    font-style: italic;
                }
            }
        }
        .right{
            flex: 0.5;
            background-color: $mainColor;
            color: whitesmoke;
   
            .content{
                margin: 20px 50px;
                h3{
                    margin-top: 20px;
                    font-weight: 500;
                    color: whitesmoke;
                }
                .nfont{
                    font-weight: 300;
                    color: white;
                }
                .link{
                    font-weight: 500;
                    font-style: italic;
                }
            }
        }
    }
}