@import "../../global.scss";

.achievements{
    display: flex;
    flex-direction: column;
    background-color: $mainColor;

    .top{
        flex: 0.2;
        text-align: center;
        
        background-color: $mainColor;
        color: white;
        display: flex;
        .left{
            flex: 0.5;
            margin-top: 25px;
            margin-bottom: 10px;
        }
        .right{
            flex: 0.5;
            background-color: whitesmoke;
        }
    }

    .bottom{
        flex: 0.8;
        display: flex;
        .left{
            flex: 0.5;
            background-color: $mainColor;
            color: white;
        }
        .right{
            flex: 0.5;
            color: $mainColor;
            background-color: whitesmoke;
        }
        .left, .right{
            .content{
                li{
                    margin-bottom: 20px;
                    margin-left: 20px;
                    margin-right: 20px;
                    font-size: 18px;
                }
            }
        }
    }
}