@import "../../global.scss";

.fullcontent{
    display: flex;
    flex-direction: column;
    .topic{
        background-color: $mainColor;
        color: white;
        flex: 0.1;
        text-align: center;
        display: flex;
        .left{
            flex: 0.5;
            text-align: center;
        }
        .right{
            flex: 0.5;
            background-color: whitesmoke;
        }
       h1{
        margin-top: 10px;
       }

    }
    .experience{
        display: flex;
        flex: 0.9;
        
        .left{
            flex: 0.5;
            color: whitesmoke;
            background-color: $mainColor;
            .contents{
                margin-left: 40px;
                margin-top: 28px;
                margin-right: 30px;
                .title{
                    color: rgb(197, 197, 241);
                   
                }
                .content1{
                    margin-bottom: 15px;
                }

                .content1, .content2{

                    h1{
                        margin-bottom: 20px;
                        text-align: center;
                        
                    }
                    h3{
                        margin-bottom: 20px;
                    }
                    ul{
                        li{
                            margin-bottom: 15px;
                        }
                    }
                }
            }
        }
        .right{
            flex: 0.5;
            color: $mainColor;
            background-color: whitesmoke;
            .contents{
                margin-left: 40px;
                margin-top: 35px;
                margin-right: 50px;
                .content1{
                    h1{
                        margin-bottom: 30px;
                    }
                    h3{
                        margin-bottom: 20px;
                        .ins1{
                            font-weight: 400;
                        }
                    }
                    ul{
                        li{
                            margin-bottom: 20px;
                        }
                    }
                    .github{
                        font-style: italic;
                        
                    }
                }
            }




        }
    }
}