@import "../../global.scss";

.education{
    display: flex;
    flex-direction: column;
   .up{
        flex: 0.13;
        background-color: $mainColor;
        color: white;
        display: flex;
        .left{
            flex: 0.5;
            color: $mainColor;
            background-color: white;
            
        }
        .right{
            flex: 0.5;
            
        }
        h1{
            text-align: center;
            margin-top: 15px;
            margin-bottom: 15px;
        }
    }

    .down{
        flex: 0.87;
        display: flex;

        .left{
            display: flex;
            flex-direction: column;
            flex: 0.5;
            .top{
                flex: 0.4; 
                background-color: white;

                .university{
                    margin-top: 1px;
                    margin-left: 40px;
                    margin-right: 10px;
                    h2{
                        margin-top: 30px;
                    }
                    .h3-1{
                        margin-top: 10px;
                        font-weight: 600;
                    }
                    h3{
                        font-weight: 400;
                    }
                }
            }
            .bottom{
                flex: 0.6;
                background-color: whitesmoke;
                .school{
                    margin-top: 30px;
                    margin-left: 40px;
                    margin-right: 10px;
                    h1{
                        text-align: center;
                    }
                    h2{
                        margin-top: 30px;
                    }
                    .h3-1 , .h3-2{
                        margin-top: 30px;
                        font-weight: 600;
                    }
                    h3{
                        font-weight: 400;
                    }
                }

            }
        }

        .right{
            flex: 0.5;
            background-color: $mainColor;
            color: white;
            h1{
                margin-bottom: 30px;
            }
            .certifications{
                margin-top: 18px;
                margin-left: 40px;
                margin-right: 30px;

                .title{
                    color: rgb(197, 197, 241);
                    font-size: 25px;
                }

                .certificates{
                    margin-top: 30px;

                    h3{
                        font-weight: 400;
                        
                    }
                    .link{
                        font-style: italic;
                        color:cornsilk;
                    }
                }
            }

        }

    }
}