@import "../../global.scss";

.topbar{
    width: 100%;
    height: 70px;
    background-color: whitesmoke;
    color: $mainColor;
    position: fixed;
    top: 0;
    z-index: 3;
    transition: all 1s ease;

    .wrapper{
    padding: 10px 30px;
    display: flex; 
    align-items: center;
    justify-content: space-between;

        .left{
                display: flex;
                align-items: center;

                .logo{
                    font-size: 35px;
                    font-weight: 700;
                    text-decoration: none;
                    color: inherit;
                    margin-right: 40px;
                    font-family: cursive;
                    font-style: italic;
                }

                .itemContainer{
                    display: flex;
                    align-items: center;
                    margin-right: 30px;

                    .icon{
                        font-size: 20px;
                        margin-right: 4px;
                    }

                    span{
                        font-size: 15px;
                        font-weight: 400;
                    }

                    a{
                        text-decoration: none; 
                    }
                }
        }

        .right{
                .hamburger{
                    width: 32px;
                    height: 25px;
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
                    cursor: pointer;

                    span{
                        width: 100%;
                        height: 3px;
                        background-color: $mainColor;
                        transform-origin: left;
                        transition: all 1s ease;
                    }
                }
        }
        
    }


    &.active{
        background-color: $mainColor;
        color: white;
        .itemContainer{
            a{
                color: white;
            }
        }

        .hamburger{

            span{
                &:first-child{
                    background-color: white;
                    transform: rotate(45deg);
                }
                &:nth-child(2){
                    opacity: 0;
                }
                &:last-child{
                    background-color: white;
                    transform: rotate(-45deg);
                }
            }


        }

    }

}